import React from 'react';

const DEBUG = false;

const SERVER_LINK = DEBUG ? "http://hoatuoituongy.tk:8002" : "https://lavenshop.tk:8002";
const WEB_LINK = DEBUG ? "http://hoatuoituongy.tk:8002" : "https://lavenconfession.tk";
const MEDIA_LINK = SERVER_LINK + '/media/';

function findWithAttr(array, attr, value) {
    for(var i = 0; i < array.length; i += 1) {
        if(array[i][attr] === value) {
            return i;
        }
    }
    return -1;
}

function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

function deleteInObject(array, attr, value) {
    array.splice(findWithAttr(array, attr, value), 1);

    return true;
}

function randomStr(length) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
       result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
 }

export {
    SERVER_LINK,
    WEB_LINK,
    findWithAttr,
    deleteInObject,
    randomStr,
    MEDIA_LINK,
    numberWithCommas
}